import React, { ReactElement, ReactNode, useEffect, useRef, useState } from "react";

/** KeepAlive 组件 */
interface KeepAliveProps {
    activeKey: string; // 当前激活的组件 Key
    children: ReactElement; // 需要缓存的组件
}

const cache = new Map<string, ReactNode>(); // 组件缓存

const KeepAlive: React.FC<KeepAliveProps> = ({ activeKey, children }) => {
    const ref = useRef<HTMLDivElement>(null);
    const [, forceUpdate] = useState({});

    useEffect(() => {
        // 确保当前组件已经缓存
        if (!cache.has(activeKey)) {
            cache.set(activeKey, children);
        }
        // 强制刷新
        forceUpdate({});
    }, [activeKey, children]);

    return (
        <div ref={ref}>
            {Array.from(cache.entries()).map(([key, component]) => (
                <div
                    key={key}
                    style={{ display: key === activeKey ? "block" : "none" }} // 仅展示当前激活的组件
                >
                    {component}
                </div>
            ))}
        </div>
    );
};

export default KeepAlive;
